import React from 'react';
import SideNav from 'react-simple-sidenav'
import SidNavItem from './side_nav_item';
const Nav = (props) => {
    return (
       <SideNav
            showNav={props.showNav}
            onHideNav={props.onHideNav}
            navStyle={{
                background:"#242424",
                maxWidth:"220px"
            }}
       >
           <SidNavItem/>
       </SideNav>
    )
};
export default Nav;